.range {
  -webkit-appearance: none;
  vertical-align: middle;
  outline: none;
  border: none;
  padding: 0;
  background: none;
}

.range::-webkit-slider-runnable-track {
  background-color: #d7dbdd;
  height: 6px;
  border-radius: 3px;
  border: 1px solid transparent;
}

.counter {
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  /* transform: translateY(-50%); */
  padding: 0 5px;
  top: 10%;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.843);
  font-size: calc(10px + (18 - 10) * ((100vw - 320px) / (1600 - 320)));
  /* font-size: 10px; */
  -webkit-animation: Color 3s infinite;
          animation: Color 3s infinite;
  border-radius: 15px;
  /* z-index: -1; */
}

@-webkit-keyframes Color {
  0% {
    color: rgba(0, 0, 0, 0.9);
  }

  20% {
    color: rgb(0, 0, 0);
  }

  40% {
    color: rgba(0, 0, 0, 0.92);
  }

  60% {
    color: rgba(0, 0, 0, 0.93);
  }

  80% {
    color: rgba(0, 0, 0, 0.95);
  }

  100% {
    color: rgba(0, 0, 0, 0.99);
  }
}

@keyframes Color {
  0% {
    color: rgba(0, 0, 0, 0.9);
  }

  20% {
    color: rgb(0, 0, 0);
  }

  40% {
    color: rgba(0, 0, 0, 0.92);
  }

  60% {
    color: rgba(0, 0, 0, 0.93);
  }

  80% {
    color: rgba(0, 0, 0, 0.95);
  }

  100% {
    color: rgba(0, 0, 0, 0.99);
  }
}

.range[disabled]::-webkit-slider-runnable-track {
  border: 1px solid #d7dbdd;
  background-color: transparent;
  opacity: 0.4;
}

.range::-moz-range-track {
  background-color: #c8cacbaa;
  height: 7px;
  border-radius: 15px;
}

.range::-ms-track {
  color: transparent;
  border: none;
  background: none;
  height: 6px;
}

.range::-ms-fill-lower {
  background-color: #d7dbdd;
  border-radius: 3px;
}

.range::-ms-fill-upper {
  background-color: #d7dbdd;
  border-radius: 3px;
}

.range::-ms-tooltip {
  display: none;
  /* display and visibility only */
}

.range::-moz-range-thumb {
  height: 15px;
  width: 15px;
  border: none;
  background-color: #ef2221;
}

